.mtrl {
  position: relative;
}
.mtrl-fixed {
  position: fixed;
}
.mtrl-raised {
  box-shadow: 0 2px 1px rgba(0,0,0,0.1), 0 1px 3px rgba(0,0,0,0.3);
}
.mtrl-btn {
  transition: background-color 0.3s, box-shadow 0.3s, color 0.3s;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-transform: uppercase;
  vertical-align: middle;
  display: inline-block;
  white-space: nowrap;
  border-radius: 3px;
  padding: 11px 22px;
  text-align: center;
  position: relative;
  line-height: 20px;
  overflow: hidden;
  cursor: pointer;
  font-size: 15px;
  margin: 5px 0;
  border: none;
}
.mtrl-btn.mtrl-raised {
  top: 0;
  left: 0;
  right: 0;
}
.mtrl-btn.mtrl-raised:Hover {
  top: -1px;
  box-shadow: 0 2px 3px rgba(0,0,0,0.1), 0 4px 8px rgba(0,0,0,0.3);
}
.mtrl-btn .mtrl-flat {
  color: #555;
  font-size: 18px;
  font-weight: 700;
  box-shadow: none;
  background-color: transparent;
}
.mtrl-btn .mtrl-flat:Hover {
  color: #333;
  background-color: rgba(255,255,255,0.22);
}
